import Layout from "../componenets/Layout/Layout";
import React, { useEffect, useState } from "react";
import axios from "axios";
import { message } from "antd";
import { useNavigate } from "react-router-dom";

const New = () => {
  const navigate = useNavigate()
  const [formData, setFormData] = useState({
    userId: "",
    amount: "",
    type: "0", 
    description: "",
  });
  useEffect(() => {
    var userData = localStorage.getItem("user");
    var userObject = JSON.parse(userData);
    setFormData({
      ...formData,
      userId: userObject._id,
    });
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post(
        "/api/transaction/add",
        formData
      );

      if (response.data.success) {
        // Login was successful, you can handle the success here
        setFormData({
          userId: "",
          amount: "",
          category: "",
          type: "income", // Reset to "income"
          description: "",
        });
        navigate("/");
        message.success(response.data.message);
      } else {
        // Login failed, you can handle the error here
        message.error(response.data.message);
      }
    } catch (error) {
      message.error("Error during login:", error);
    }
  };

  return (
    <Layout>
      <div className="row">
        <div className="col-6 offset-3 my-3">
          <div className="bg-light p-3">
            <div className="text-center p-4">
              <h2>Add Transaction</h2>
            </div>
            <form onSubmit={handleSubmit}>
              Amount:
              <input
                type="number"
                name="amount"
                id="amount"
                className="form-control"
                value={formData.amount}
                onChange={handleInputChange}
              />
            
              Transaction Type:
              <div>
                <label>
                  <input
                    type="radio"
                    name="type"
                    value="1"
                    checked={formData.type === "1"}
                    onChange={handleInputChange}
                    
                  />
                  Income
                </label>
                <label>
                  <input
                    type="radio"
                    name="type"
                    value="0"
                    checked={formData.type === "0"}
                    onChange={handleInputChange}
                  />
                  Expense
                </label>
              </div>
              Description:
              <textarea
                name="description"
                id="description"
                className="form-control"
                value={formData.description}
                onChange={handleInputChange}
              />
              <button type="submit" className="btn btn-primary mt-3">
                Add Transaction
              </button>
            </form>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default New;
