import React, { useEffect, useState } from "react";
import Layout from "../componenets/Layout/Layout";
import { useNavigate } from "react-router-dom";
import { message } from "antd";
import axios from "axios";
import DeleteIcon from "@mui/icons-material/Delete";
import { Progress } from "antd";
import Swal from "sweetalert2";
const HomePage = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  const [userId, setUserId] = useState(null);
  const [filter, setFilter] = useState("all");
  const [viewMode, setViewMode] = useState("table");  
  const [f, setf] = useState(true);
  useEffect(() => {
    if (!localStorage.getItem("user")) {
      navigate("/login");
    } else {
      const user = JSON.parse(localStorage.getItem("user"));
      setUserId(user._id);
    }
  }, [navigate]);

  useEffect(() => {
    if (userId !== null) {
      getTransaction(userId);
    }
  }, [userId, filter, viewMode,f]);

  const formatDate = (dateString) => {
    const options = { month: "short", day: "numeric", year: "numeric" };
    return new Date(dateString).toLocaleDateString(undefined, options);
  };

  const isSameWeek = (date1, date2) => {
    const firstDayOfWeek = 1;
    const daysInWeek = 7;
    const currentWeek = Math.ceil(
      (date1.getDate() - date1.getDay() + firstDayOfWeek + daysInWeek) /
        daysInWeek
    );
    const transactionWeek = Math.ceil(
      (date2.getDate() - date2.getDay() + firstDayOfWeek + daysInWeek) /
        daysInWeek
    );
    return currentWeek === transactionWeek;
  };
  const deleteTransaction = (transactionId) => {
    // alert(transactionId)
    setLoading(true)
    Swal.fire({
      title: "Do you want to Delete?",
      showDenyButton: true,
      showCancelButton: false,
      confirmButtonText: "Yes",
      denyButtonText: `No`,
    }).then((result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
        setLoading(false)
setTimeout(() => {
        setf(!f);
  
}, 1500);        deleteNow(transactionId);
      } else if (result.isDenied) {
        Swal.fire("Transaction Not Deleted", "", "error");
        setLoading(false);
      }
    });
  }
  


  const deleteNow = async (transactionId) => {
    try {
      const response = await axios.delete(
        `/api/transaction/delete/${transactionId}`
      );

      if (response.data.success) {
        Swal.fire("Transaction  Deleted", "", "success");

      } else {
        message.error(response.data.message || "An error occurred");
      }
    } catch (error) {
      message.error("An error occurred");
    }
  };
  const getTransaction = async (userId) => {
    try {
      const response = await axios.post(
        `/api/transaction/get/${userId}`
      );

      if (response.data.success) {
        message.success("Data fetched successfully");
        const formattedData = response.data.transactions.map((transaction) => ({
          ...transaction,
          createdAt: formatDate(transaction.createdAt),
        }));
        setData(formattedData);
      } else {
        message.error(response.data.message || "An error occurred");
      }
    } catch (error) {
      message.error("An error occurred");
    } finally {
      setLoading(false);
    }
  };

  const toggleViewMode = () => {
    setViewMode(viewMode === "table" ? "chart" : "table");
  };

  const applyDateFilter = () => {
    const currentDate = new Date();
    const filteredData = data.filter((transaction) => {
      const transactionDate = new Date(transaction.createdAt);

      switch (filter) {
        case "lastDay":
          const previousDay = new Date(currentDate);
          previousDay.setDate(currentDate.getDate() - 1);

          return (
            transactionDate.getDate() === previousDay.getDate() &&
            transactionDate.getMonth() === previousDay.getMonth() &&
            transactionDate.getFullYear() === previousDay.getFullYear()
          );

        case "thisWeek":
          return isSameWeek(currentDate, transactionDate);

        case "lastWeek":
          const lastWeekStartDate = new Date(currentDate);
          lastWeekStartDate.setDate(currentDate.getDate() - 7);

          return (
            isSameWeek(lastWeekStartDate, transactionDate) &&
            !isSameWeek(currentDate, transactionDate)
          );

        case "thisMonth":
          return (
            currentDate.getMonth() === transactionDate.getMonth() &&
            currentDate.getFullYear() === transactionDate.getFullYear()
          );

        case "last48Hours":
          const last48HoursStartDate = new Date(currentDate);
          last48HoursStartDate.setHours(currentDate.getHours() - 48);

          return transactionDate >= last48HoursStartDate;

        case "thisYear":
          return currentDate.getFullYear() === transactionDate.getFullYear();

        default:
          return true;
      }
    });

    return filteredData;
  };

  const filteredData = applyDateFilter();
const incomeTransactions = filteredData.filter(
  (transaction) => transaction.type === 1
);
const expenseTransactions = filteredData.filter(
  (transaction) => transaction.type === 0
  );
  const totalAmount = filteredData.reduce((total, transaction) => {
    return total + transaction.amount;
  }, 0);
  const totalIncomeAmount = incomeTransactions.reduce((total, transaction) => {
    return total + transaction.amount;
  }, 0);

   const totalExpenseAmount = expenseTransactions.reduce(
     (total, transaction) => {
       return total + transaction.amount;
     },
     0
  );
  
  const totalTransactions = filteredData.length;
  const incomePercentage = (incomeTransactions.length / totalTransactions) * 100;
  const incomeAmount = (totalIncomeAmount / totalAmount) * 100;
  const expenseAmount = (totalExpenseAmount / totalAmount) * 100;
const expensePercentage =
  (expenseTransactions.length / filteredData.length) * 100;
  return (
    <Layout>
      <h1>Transaction Data</h1>
      <div className="row">
        <div className="col-sm-6 col-md-10 col-xm-4 offset-1">
          <div>
            <label>Filter By Date:</label>
            <div>
              <button
                className={`btn btn-sm ${
                  filter === "all" ? "btn-primary" : "btn-outline-primary"
                }`}
                onClick={() => setFilter("all")}
              >
                All
              </button>
              <button
                className={`btn btn-sm ${
                  filter === "lastDay" ? "btn-primary" : "btn-outline-primary"
                }`}
                onClick={() => setFilter("lastDay")}
              >
                Last Day
              </button>

              <button
                className={`btn btn-sm ${
                  filter === "thisMonth" ? "btn-primary" : "btn-outline-primary"
                }`}
                onClick={() => setFilter("thisMonth")}
              >
                This Month
              </button>

              <button
                className={`btn btn-sm ${
                  filter === "thisYear" ? "btn-primary" : "btn-outline-primary"
                }`}
                onClick={() => setFilter("thisYear")}
              >
                This Year
              </button>
            </div>
          </div>
          <div>
            <label>View Mode:</label>
            <button
              className={`btn btn-sm ${
                viewMode === "table" ? "btn-primary" : "btn-outline-primary"
              }`}
              onClick={toggleViewMode}
            >
              Table
            </button>
            <button
              className={`btn btn-sm ${
                viewMode === "chart" ? "btn-primary" : "btn-outline-primary"
              }`}
              onClick={toggleViewMode}
            >
              Chart
            </button>
          </div>
          {loading ? (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "300px",
              }}
            >
              <div className="spinner-border text-primary" role="status">
                <span className="visually-hidden">Loading...</span>
              </div>
            </div>
          ) : (
            <div>
              {viewMode === "table" ? (
                <table className="table" style={{ width: "550px" }}>
                  <thead>
                    <tr>
                      <th style={{ width: "40px" }}>Transaction ID</th>
                      <th className="" style={{ width: "40px" }}>
                        Amount
                      </th>
                      <th style={{ width: "60px" }}>Description</th>
                      <th style={{ width: "40px" }}>Type</th>
                      <th style={{ width: "99px" }}>Date</th>
                      <th>Actions</th>
                      {/* Add more table headers as needed */}
                    </tr>
                  </thead>
                  <tbody>
                    {filteredData.map((transaction, index) => (
                      <tr key={transaction._id}>
                        <td>{index + 1}</td>
                        <td>{transaction.amount}</td>
                        <td>{transaction.description}</td>
                        <td>{transaction.type == 1 ? "Income " : "Expense"}</td>
                        <td>{transaction.createdAt}</td>
                        <td>
                          <button
                            className="btn btn-danger"
                            onClick={() => deleteTransaction(transaction._id)}
                          >
                            {" "}
                            <DeleteIcon />
                          </button>
                        </td>
                        {/* Add more table cells for other data */}
                      </tr>
                    ))}
                  </tbody>
                </table>
              ) : (
                <div>
                  {/* Placeholder for the Chart */}
                  <div className="row">
                    <div className="col-4 offset-2">
                      <div className="card">
                        <div className="card-body">
                          <span className="text-danger">
                            All Transactions: {filteredData.length}
                          </span>{" "}
                          <hr />
                          <span className="text-danger">
                            Expense: {expenseTransactions.length}
                          </span>
                          <hr />
                          <span className="text-success">
                            Income: {incomeTransactions.length}
                          </span>
                        </div>
                        <div className="card-footer">
                          <Progress
                            type="circle"
                            percent={incomePercentage.toFixed(0)}
                            strokeColor={"green"}
                          />
                          <Progress
                            type="circle"
                            percent={expensePercentage.toFixed(0)}
                            strokeColor={"red"}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-4 offset-2">
                      <div className="card">
                        <div className="card-body">
                          <span className="text-danger">
                            All Amount: {totalAmount}
                          </span>{" "}
                          <hr />
                          <span className="text-danger">
                            Expense: {totalExpenseAmount}
                          </span>
                          <hr />
                          <span className="text-success">
                            Income: {totalIncomeAmount}
                          </span>
                        </div>
                        <div className="card-footer">
                          <Progress
                            type="circle"
                            percent={incomeAmount.toFixed(0)}
                            strokeColor={"green"}
                          />
                          <Progress
                            type="circle"
                            percent={expenseAmount.toFixed(0)}
                            strokeColor={"red"}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </Layout>
  );
};

export default HomePage;
